.div-center {
    display: table;
    margin: 0 auto;
}

.div-center img {
    width: 10rem;
}

.buttons-group {
    display: flex;
}

.buttons-group button:first-child {
    margin-right: 8px;
}

a, div, img, p, button {
    -moz-user-select: none;
     -ms-user-select: none;
         user-select: none;
    -webkit-user-select: none;
    -webkit-touch-callout: none;
    -webkit-tap-highlight-color: transparent;
}

.startbutt{
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    margin:0 auto;
    padding: 50px;
    position: center;
    border-radius: 500%;
    background: linear-gradient(blue,white) fixed;
    color: black;
}
